import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

var recents = [];
var str = "Last 10 numbers appear here...";
var pickedNumbers = Array(91).fill(false);
var alwaysRandom = false;
function Square(props) {
  let cssClass = "";
  if (props.isChosen) {
    cssClass += "selected-square";
  }
  else {
    cssClass += "square";
  }
  return (
    <button id={"number-" + props.value} className={`${cssClass}`} onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {
  constructor(props) {
    super(props);
    const N = 90;
    this.state = {
      squares: Array.from(Array(N), (_, index) => index + 1),
      isChosen: Array(N).fill(false),
      renderRef: props.renderRef,
    }
  }

  renderSquare(i, isChosen) {
    return (
      <Square value={this.state.squares[i]}
        onClick={() => this.handleClick(i)}
        isChosen={isChosen}
      />
    );
  }

  handleClick(i) {
    const isChosen = this.state.isChosen;
    isChosen[i] = !isChosen[i];
    this.setState({
      isChosen: isChosen,
    });
    if (isChosen[i]) {
      var audioElement = document.createElement('audio');
      audioElement.setAttribute('src', 'success_notification.mp3');
      audioElement.play();
      pickedNumbers[i + 1] = true;
      recents.push(i + 1)
    } else {
      var audioElement = document.createElement('audio');
      audioElement.setAttribute('src', 'mario_death.mp3');
      audioElement.play();
      recents = recents.filter(e => e != (i + 1));
      pickedNumbers[i + 1] = false;
    }
    this.state.renderRef();
  }

  createBoard(row, col) {
    const board = [];
    let cellCounter = 0;

    for (let i = 0; i < row; i += 1) {
      const row = [];
      for (let j = 0; j < col; j += 1) {
        row.push(this.renderSquare(cellCounter++, this.state.isChosen[cellCounter - 1]));
      }
      board.push(<div key={i} className="board-row">{row}</div>);
    }

    return board;
  }

  render() {
    return <div>{this.createBoard(9, 10)}</div>;
  }
}

